iEAEiE;AACjE;EACE,eAAe;EACf,sCAAsC;EACtC,eAAe;EACf,YAAY;AACd;;AAEA;EACE,cAAc;EACd,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB,WAAW;EACX,gCAAgC;AAClC;;AAEA;EACE,eAAe;EACf,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,0CAA0C;EAC1C,eAAe;EACf,+BAA+B;EAC/B,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,kBAAkB;EAClB,oBAAoB;AACtB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,UAAU;EACV,SAAS;AACX;;AAEA;EACE,kBAAkB;EAClB,6BAA6B;EAC7B,eAAe;EACf,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,+BAA+B;EAC/B,gBAAgB;EAChB,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,WAAW;AACb","sourcesContent":["/* Fonts */\n:root {\n  --font-default: \"Open Sans\", system-ui, -apple-system, \"Segoe UI\", Roboto, \"Helvetica Neue\", Arial, \"Noto Sans\", \"Liberation Sans\", sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\",\n    \"Segoe UI Symbol\", \"Noto Color Emoji\";\n  --font-primary: \"Montserrat\", sans-serif;\n  --font-secondary: \"Poppins\", sans-serif;\n}\n\n/* Colors */\n:root {\n  --color-default: #222222;\n  --color-primary: #5bc17f;\n  --color-secondary: #f85a40;\n}\n\n/* Smooth scroll behavior */\n:root {\n  scroll-behavior: smooth;\n}\n\n/*--------------------------------------------------------------\n  # General\n  --------------------------------------------------------------*/\nbody {\n  font-family: var(--font-default);\n  color: var(--color-default);\n}\n\na {\n  color: var(--color-default);\n  text-decoration: none;\n}\n\na:hover {\n  color: #fff;\n  text-decoration: none;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: var(--font-primary);\n}\n\n/*--------------------------------------------------------------\n  # Disable aos animation delay on mobile devices\n  --------------------------------------------------------------*/\n@media screen and (max-width: 768px) {\n  [data-aos-delay] {\n    transition-delay: 0 !important;\n  }\n}\n\n/*--------------------------------------------------------------\n  # Header\n  --------------------------------------------------------------*/\n\n.header {\n  transition: all 0.5s;\n  z-index: 997;\n  height: 90px;\n}\n\n.header.sticked {\n  position: fixed;\n  top: 0;\n  right: 0;\n  left: 0;\n  height: 70px;\n  box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);\n}\n\n.header .logo img {\n  max-height: 30px;\n  margin-right: 10px;\n}\n\n.header .logo h1 {\n  font-size: 30px;\n  margin: 0;\n  font-weight: 600;\n  letter-spacing: 0.8px;\n  font-weight: bold;\n  color: #2E2E2E;\n  font-family: var(--font-primary);\n}\n\n.sticked-header-offset {\n  margin-top: 70px;\n}\n\n/*--------------------------------------------------------------\n  # Desktop Navigation\n  --------------------------------------------------------------*/\n@media (min-width: 1280px) {\n  .navbar {\n    padding: 0;\n  }\n\n  .navbar ul {\n    margin: 0;\n    padding: 0;\n    display: flex;\n    list-style: none;\n    align-items: center;\n  }\n\n  .navbar li {\n    position: relative;\n  }\n\n  .navbar > ul > li {\n    white-space: nowrap;\n    padding: 10px 0 10px 28px;\n  }\n\n  .navbar a,\n  .navbar a:focus {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 0 3px;\n    font-family: var(--font-secondary);\n    font-size: 16px;\n    font-weight: 600;\n    color: rgba(46, 46, 46, 0.6);\n    white-space: nowrap;\n    transition: 0.3s;\n    position: relative;\n  }\n\n  .navbar a i,\n  .navbar a:focus i {\n    font-size: 12px;\n    line-height: 0;\n    margin-left: 5px;\n  }\n\n  .navbar > ul > li > a:before {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 2px;\n    bottom: -6px;\n    left: 0;\n    background-color: var(--color-secondary);\n    visibility: hidden;\n    width: 0px;\n    transition: all 0.3s ease-in-out 0s;\n  }\n\n  .navbar a:hover:before,\n  .navbar li:hover > a:before,\n  .navbar .active:before {\n    visibility: visible;\n    width: 100%;\n  }\n\n  .navbar a:hover,\n  .navbar .active,\n  .navbar .active:focus,\n  .navbar li:hover > a {\n    color: #5bc17f;\n  }\n\n  .navbar .dropdown ul {\n    display: block;\n    position: absolute;\n    left: 28px;\n    top: calc(100% + 30px);\n    margin: 0;\n    padding: 10px 0;\n    z-index: 99;\n    opacity: 0;\n    visibility: hidden;\n    background: #fff;\n    box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);\n    transition: 0.3s;\n    border-radius: 4px;\n  }\n\n  .navbar .dropdown ul li {\n    min-width: 200px;\n  }\n\n  .navbar .dropdown ul a {\n    padding: 10px 20px;\n    font-size: 15px;\n    text-transform: none;\n    font-weight: 600;\n    color: #5bc17f;\n  }\n\n  .navbar .dropdown ul a i {\n    font-size: 12px;\n  }\n\n  .navbar .dropdown ul a:hover,\n  .navbar .dropdown ul .active:hover,\n  .navbar .dropdown ul li:hover > a {\n    color: var(--color-secondary);\n  }\n\n  .navbar .dropdown:hover > ul {\n    opacity: 1;\n    top: 100%;\n    visibility: visible;\n  }\n\n  .navbar .dropdown .dropdown ul {\n    top: 0;\n    left: calc(100% - 30px);\n    visibility: hidden;\n  }\n\n  .navbar .dropdown .dropdown:hover > ul {\n    opacity: 1;\n    top: 0;\n    left: 100%;\n    visibility: visible;\n  }\n}\n\n@media (min-width: 1280px) and (max-width: 1600px) {\n  .navbar .dropdown .dropdown ul {\n    left: -90%;\n  }\n\n  .navbar .dropdown .dropdown:hover > ul {\n    left: -100%;\n  }\n}\n\n@media (min-width: 1280px) {\n  .mobile-nav-show,\n  .mobile-nav-hide {\n    display: none;\n  }\n}\n\n/*--------------------------------------------------------------\n  # Mobile Navigation\n  --------------------------------------------------------------*/\n@media (max-width: 1279px) {\n  .navbar {\n    position: fixed;\n    top: 0;\n    right: -100%;\n    width: 100%;\n    max-width: 400px;\n    bottom: 0;\n    transition: 0.3s;\n    z-index: 9997;\n  }\n\n  .navbar ul {\n    position: absolute;\n    inset: 0;\n    padding: 50px 0 10px 0;\n    margin: 0;\n    background: rgba(0, 131, 116, 0.9);\n    overflow-y: auto;\n    transition: 0.3s;\n    z-index: 9998;\n  }\n\n  .navbar a,\n  .navbar a:focus {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 10px 20px;\n    font-family: var(--font-primary);\n    font-size: 15px;\n    font-weight: 600;\n    color: rgba(255, 255, 255, 0.7);\n    white-space: nowrap;\n    transition: 0.3s;\n  }\n\n  .navbar a i,\n  .navbar a:focus i {\n    font-size: 12px;\n    line-height: 0;\n    margin-left: 5px;\n  }\n\n  .navbar a:hover,\n  .navbar .active,\n  .navbar .active:focus,\n  .navbar li:hover > a {\n    color: #5bc17f;\n  }\n\n  .navbar .getstarted,\n  .navbar .getstarted:focus {\n    background: var(--color-primary);\n    padding: 8px 20px;\n    border-radius: 4px;\n    margin: 15px;\n    color: #5bc17f;\n  }\n\n  .navbar .getstarted:hover,\n  .navbar .getstarted:focus:hover {\n    color: #5bc17f;\n    background: rgba(0, 131, 116, 0.8);\n  }\n\n  .navbar .dropdown ul,\n  .navbar .dropdown .dropdown ul {\n    position: static;\n    display: none;\n    padding: 10px 0;\n    margin: 10px 20px;\n    transition: all 0.5s ease-in-out;\n    background-color: #007466;\n    border: 1px solid #006459;\n  }\n\n  .navbar .dropdown > .dropdown-active,\n  .navbar .dropdown .dropdown > .dropdown-active {\n    display: block;\n  }\n\n  .mobile-nav-show {\n    color: rgba(91, 193, 127, 0.6);\n    font-size: 28px;\n    cursor: pointer;\n    line-height: 0;\n    transition: 0.5s;\n    z-index: 9999;\n    margin-right: 10px;\n  }\n\n  .mobile-nav-hide {\n    color: #fff;\n    font-size: 32px;\n    cursor: pointer;\n    line-height: 0;\n    transition: 0.5s;\n    position: fixed;\n    right: 20px;\n    top: 20px;\n    z-index: 9999;\n  }\n\n  .mobile-nav-active {\n    overflow: hidden;\n  }\n\n  .mobile-nav-active .navbar {\n    right: 0;\n  }\n\n  .mobile-nav-active .navbar:before {\n    content: \"\";\n    position: fixed;\n    inset: 0;\n    background: rgba(0, 106, 93, 0.8);\n    z-index: 9996;\n  }\n}\n\n/*--------------------------------------------------------------\n  # Hero Section\n  --------------------------------------------------------------*/\n.hero {\n  width: 100%;\n  position: relative;\n  /* background: var(--color-primary); */\n  padding: 150px 0 190px 0;\n}\n\n/* .img-fluid {\n  margin-left: 120px;\n} */\n\n@media (min-width: 1365px) {\n  .hero {\n    background-attachment: fixed;\n  }\n}\n\n.hero h2 {\n  font-size: 48px;\n  font-weight: 700;\n  margin-bottom: 20px;\n  color: #2E2E2E;\n  line-height: 1.5; \n}\n\n.hero span {\n  color: #5bc17f;\n}\n\n.hero .btn-watch-video i.bi-calculator {\n  color: #5bc17f;\n}\n\n.hero p {\n  color: rgba(46, 46, 46, 0.6);\n  font-weight: 400;\n  margin-bottom: 35px;\n  line-height: 1.9; \n}\n\n.hero .btn-get-started {\n  font-family: var(--font-primary);\n  font-weight: 500;\n  font-size: 15px;\n  letter-spacing: 1px;\n  display: inline-block;\n  padding: 14px 40px;\n  border-radius: 50px;\n  transition: 0.3s;\n  color: #5bc17f;\n  background: rgba(255, 255, 255, 0.1);\n  box-shadow: 0 0 15px rgba(0, 0, 0, 0.08);\n  border: 2px solid rgba(255, 255, 255, 0.1);\n}\n\n.hero .btn-get-started:hover {\n  border-color: rgba(91, 193, 127, 0.6);\n}\n\n.hero .my-custom-icon {\n  color: #5bc17f;\n}\n\n.hero .btn-watch-video {\n  font-size: 16px;\n  transition: 0.5s;\n  margin-left: 25px;\n  color: #5bc17f;\n  font-weight: 600;\n}\n\n.hero .btn-watch-video i {\n  color: rgba(255, 255, 255, 0.5);\n  font-size: 32px;\n  transition: 0.3s;\n  line-height: 0;\n  margin-right: 8px;\n}\n\n.hero .btn-watch-video:hover i {\n  color: #fff;\n}\n\n@media (max-width: 640px) {\n  .hero h2 {\n    font-size: 36px;\n  }\n\n  .hero .btn-get-started,\n  .hero .btn-watch-video {\n    font-size: 14px;\n  }\n}\n\n.hero .icon-boxes {\n  padding-bottom: 60px;\n}\n\n@media (min-width: 1200px) {\n  .hero .icon-boxes:before {\n    content: \"\";\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    height: calc(50% + 20px);\n    background-color: #fff;\n  }\n}\n\n.hero .icon-box {\n  padding: 60px 30px;\n  position: relative;\n  overflow: hidden;\n  background: #008d7d;\n  box-shadow: 0 0 29px 0 rgba(0, 0, 0, 0.08);\n  transition: all 0.3s ease-in-out;\n  border-radius: 8px;\n  z-index: 1;\n  height: 100%;\n  width: 100%;\n  text-align: center;\n}\n\n.hero .icon-box .title {\n  font-weight: 700;\n  margin-bottom: 15px;\n  font-size: 24px;\n}\n\n.hero .icon-box .title a {\n  color: #fff;\n  transition: 0.3s;\n}\n\n.hero .icon-box .icon {\n  margin-bottom: 20px;\n  padding-top: 10px;\n  display: inline-block;\n  transition: all 0.3s ease-in-out;\n  font-size: 48px;\n  line-height: 1;\n  color: rgba(255, 255, 255, 0.6);\n}\n\n.hero .icon-box:hover {\n  background: #009786;\n}\n\n.hero .icon-box:hover .title a,\n.hero .icon-box:hover .icon {\n  color: #fff;\n}\n\n/*--------------------------------------------------------------\n  # Footer\n  --------------------------------------------------------------*/\n.footer {\n  font-size: 14px;\n  background-color: var(--color-primary);\n  padding: 50px 0;\n  color: white;\n}\n\n.footer .footer-info .logo {\n  line-height: 0;\n  margin-bottom: 25px;\n}\n\n.footer .footer-info .logo img {\n  max-height: 40px;\n  margin-right: 6px;\n}\n\n.footer .footer-info .logo span {\n  font-size: 30px;\n  font-weight: 700;\n  letter-spacing: 1px;\n  color: #fff;\n  font-family: var(--font-primary);\n}\n\n.footer .footer-info p {\n  font-size: 14px;\n  font-family: var(--font-primary);\n}\n\n.footer .social-links a {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  font-size: 16px;\n  color: rgba(255, 255, 255, 0.7);\n  margin-right: 10px;\n  transition: 0.3s;\n}\n\n.footer .social-links a:hover {\n  color: #fff;\n  border-color: #fff;\n}\n\n.footer h4 {\n  font-size: 16px;\n  font-weight: bold;\n  position: relative;\n  padding-bottom: 12px;\n}\n\n.footer .footer-links {\n  margin-bottom: 30px;\n}\n\n.footer .footer-links ul {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n}\n\n.footer .footer-links ul i {\n  padding-right: 2px;\n  color: rgba(0, 131, 116, 0.8);\n  font-size: 12px;\n  line-height: 0;\n}\n\n.footer .footer-links ul li {\n  padding: 10px 0;\n  display: flex;\n  align-items: center;\n}\n\n.footer .footer-links ul li:first-child {\n  padding-top: 0;\n}\n\n.footer .footer-links ul a {\n  color: rgba(255, 255, 255, 0.7);\n  transition: 0.3s;\n  display: inline-block;\n  line-height: 1;\n}\n\n.footer .footer-links ul a:hover {\n  color: #fff;\n}\n\n.footer .footer-contact p {\n  line-height: 26px;\n}\n\n.footer .copyright {\n  text-align: center;\n}\n\n.footer .credits {\n  padding-top: 4px;\n  text-align: center;\n  font-size: 13px;\n}\n\n.footer .credits a {\n  color: #fff;\n}\n"],"sourceRoot":""}]);
